import React, { useState } from 'react'
import { Container, Row, Col } from 'react-bootstrap'

import OfferSection from '../Public/offersection'
import Cloud from '../Public/cloud'
import Chart from './Chart/index'
import MyGoogleMap from './Map/googlemapreact'
import Alert from './alert'
import ResultBoard from './resultboard'


export default function MainHomepage(props) {
    const [center, setCenter] = useState([59.3293, 18.0686]);
    const [place, setPlace] = useState([]);
    const [alertstate, setAlertState] = useState(true);
    const [percent, setPercent] = useState([]);
    const [address, setAddress] = useState();
    const [inputChanged, setInputChanged] = useState(false);
    const [tipText, setTipText] = useState("Now would be a good time to charge your electrical devices!");

    const lowLevelText = [
        "Little renewables awailable right now, perhaps reschedule your coffee break",
        "Please aviod using the coffee maker right now since the energy mix is very bad",
        "Currently low on renewables, please consider not charing your devices",
        "No clean enery awailable. Do not use the kitchen appliances right now.",
        "Very low on renewable energy. Please unplug and help save the planet. "
    ];
    const midLevelText = [
        "Today is not a great day for renewables, consider holding off charging your car",
        "Little renewables awailable right now, perhaps reschedule your coffee break",
        "Currently low on renewables, please consider not charing your devices",
        "Not much clean energy right now. Please consider holding off lunch and running the dishwasher ",
        "Seems like the energy is just below recommended renewable levels. Want to wait until more renewables are available?"
    ]

    const highLevelText = [
        "This afternoon is good for renewables, charge your car after lunch for a renewable charge",
        "Now youre running on renewables! Hurry up and make some renewably charged coffee!",
        "There's a lot of renewables available - Charge your phone and plug in your computer!",
        "High on renewables! Heat your food, make some coffee and run the dishwasher. Its clean energy!",
        "Lots of renewable energy up for grabs! Now is a good time to charge devices "
    ]

    const allowState = (_allow_state) => {
        setAlertState(_allow_state);
    }

    const changeLocation = (_changed_center) => {
        setCenter(_changed_center);
    }

    const setStateOfAlert = (value) => {
        setAlertState(value);
    }

    const getPercent = (value) => {
        setPercent(value);
        computePercent(value);
        props.getPercentForHeader(value);
    }

    const getAddress = (value) => {
        setAddress(value);
    }

    const getPlace = (value) => {
        setPlace(value);
    }

    const inputChangedFunc = (value) => {
        setInputChanged(value);
    }


    const computePercent = (value) => {

        let resultScore = 0;
        value.forEach(element => {
            if (Object.keys(element)[0] != 'nonr') {
                resultScore += Object.values(element)[0];
            }
        });

        if (resultScore < 65) {
            setTipText(lowLevelText[Math.floor(Math.random() * 5)]);
        }
        else if (resultScore >= 65 && resultScore < 70) {
            setTipText(midLevelText[Math.floor(Math.random() * 5)]);
        } else {
            setTipText(highLevelText[Math.floor(Math.random() * 5)]);
        }

    }

    return (
        <div className="homepage">

            <Alert allowState={allowState} centerChanged={center} inputChanged={inputChanged} changeLocation={changeLocation} inputChangedFunc={inputChangedFunc}  getPlace={getPlace}/>

            <Chart center={center} getPercent={getPercent} place={place} />

            <Cloud percent={percent} />

            <Container className="main-tool-board">
                <Row>
                    <Col xl="4" sm="9" xs="12" >
                        <div className="map-wrapper">
                            <MyGoogleMap changeLocation={changeLocation} inputChanged={inputChangedFunc} center={center} getPlace={getPlace}/>
                        </div>
                    </Col>

                    <Col xl="4" sm="9" xs="12" >
                        <div className="center-circle">
                            <p>
                                Checking exactly what energy<br />
                                sources you are consuming in real<br />
                                time, will enable you to drastically
                                reduce your energy emissions.<br /><br />
                                Type in any city  in our tool to the<br />
                                left, and try it for yourself!

                            </p>
                        </div>
                    </Col>
                    <Col xl="4" sm="9" xs="12" >
                        <div className="result-board">
                            <ResultBoard percent={percent} />
                        </div>
                    </Col>
                </Row>

                <Row>
                    <Col >
                        <div className="big-circle">
                            <p>
                                In order to make the switch to a more<br /> greener energy consumption, we need to<br /> start by asking this very question:<br /><br />
                                <b>
                                    What energy am I
                                    consuming right now?
                                </b>
                            </p>
                        </div>
                    </Col>
                    <Col className="small-circle-wrapper">
                        <div className="small-circle">
                            <p>
                                <b>
                                    Tip of the day!
                                </b><br />
                                {tipText}
                            </p>
                        </div>
                    </Col>
                </Row>
            </Container>


            <OfferSection />
        </div>
    )
}
