import React, { Component } from 'react';
import styled from 'styled-components';

import SwedenCity from './se.json'

const Wrapper = styled.div`
  position: relative;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 20px;
  text-align:center;
`;




export default function AutoComplete(props) {
    const [autoComplete, setAutoComlete] = React.useState(null);
    const [locationGeocode, setLocationGeocode] = React.useState([]);
    const searchInput = React.useRef();
    const cityList = React.useRef();
    const cityLists = SwedenCity;

    const listItems = cityLists.map((item, key) =>
        <li key={key} onClick={() => selectCity(item)}><p key={key + "-a"}>{item.city}</p></li>
    );

    const selectCity = (item) => {
        searchInput.current.value = item.city;
        const ul = cityList.current;
        ul.style.display = "none";
        if(window.locationElement) window.locationElement.scrollIntoView({behavior: 'smooth', block: 'center'});

        props.getCenter([Number(item.lat), Number(item.lng)]);
        setTimeout(() => {
            props.getPlace([item.city, item.country]);
        }, 1500);

    }

    const clearSearchBox = () => {
        if (!searchInput.current.value) cityList.current.style.display = 'none';
    }

    window.inputSearch = searchInput.current;


    const searchList = () => {
        /** Should make alert visible */
        props.inputChanged(true);

        var input, filter, ul, li, p, i, txtValue;
        input = searchInput.current;
        input.style.display = '';
        filter = input.value.toUpperCase();
        ul = cityList.current;
        ul.style.display = ''
        li = ul.getElementsByTagName("li");
        for (i = 0; i < li.length; i++) {
            li[i].style.display = "block";
            p = li[i].getElementsByTagName("p")[0];
            txtValue = p.textContent || p.innerText;
            if (txtValue.toUpperCase().indexOf(filter) > -1) {
                li[i].style.display = "block";
            } else {
                li[i].style.display = "none";
            }
        }
        if (!searchInput.current.value) cityList.current.style.display = 'none';

    }

    return (
        <Wrapper>
            <p className="search-input-label">Find location on map</p>
            <span className="fa fa-search form-control-feedback"></span>
            <input
                className="search-input"
                ref={searchInput}
                type="text"
                onFocus={clearSearchBox}
                placeholder="Search city"
                onKeyUp={searchList}
            />

            <ul id="cityList" ref={cityList}>
                {listItems}
            </ul>
        </Wrapper>
    )
}
