import React, { useEffect, useRef, useState } from 'react';
import { Container, Modal, Button } from 'react-bootstrap';

import vector from '../../../assets/img/vector.png'
import SwedenCity from './Map/se.json'

export default function Alert(props) {
    const [show, setShow] = useState(false);
    const [message, setMessage] = useState("");
    const handleClose = () => setShow(false);
    const [error, setError] = useState(0);

    const alertElement = useRef();
    const cityLists = SwedenCity;

    const setCurrentLocation = () => {
        if ('geolocation' in navigator) {
            try {
                const successfulLookup = position => {
                    const { latitude, longitude } = position.coords;
                    fetch(`https://api.opencagedata.com/geocode/v1/json?q=${latitude}+${longitude}&key=6a80181f43a541b58495a4ddf5d90d41`)
                        .then(response => response.json())
                        .then((data) => {
                            if (data && data.results[0] && data.results[0].formatted) {
                                props.getPlace(data.results[0].formatted.split(","));
                                props.changeLocation([latitude, longitude]);
                                props.getCenter([latitude, longitude]);
                            } else setError(4);
                        })
                }

                navigator.geolocation.getCurrentPosition(successfulLookup, console.log);

            } catch (error) {
                if (error) {
                    setError(4);;
                }
            }
        }
    }

    const allow = () => {


        if ('geolocation' in navigator) {
            try {
                const successfulLookup = position => {
                    const { latitude, longitude } = position.coords;
                    fetch(`https://api.opencagedata.com/geocode/v1/json?q=${latitude}+${longitude}&key=6a80181f43a541b58495a4ddf5d90d41`)
                        .then(response => response.json())
                        .then((data) => {
                            if (data && data.results[0] && data.results[0].formatted) {
                                props.getPlace(data.results[0].formatted.split(","));
                                props.changeLocation([latitude, longitude]);
                            } else setError(4);
                        })
                }

                navigator.geolocation.getCurrentPosition(successfulLookup, console.log);
                alertElement.current.style.visibility = "hidden";
                window.inputSearch.value = '';

            } catch (error) {
                if (error) {
                    console.log(error);
                }
            }
        } else alert("Enable Navigator Geolocation !");

    }

    const dismiss = () => {
        alertElement.current.style.visibility = "hidden";
    }

    // useEffect(() => {
    //     if (props.inputChanged && alertElement.current.style && alertElement.current.style.visibility) {
    //         alertElement.current.style.visibility = 'visible';
    //     }
    // }, [props.inputChanged])

    useEffect(() => {
        window.alertElement = alertElement.current;
    })


    return (
        <Container fluid className="alert-window" >
            <div className="alert-wrapper" ref={alertElement} >
                <img className="vector" alt="vector" src={vector} />

                <div className="alert-text">.renbloc would like to use your location<br />to display the source of your energy.</div>
                <div className="btn-group">
                    <button className="alert-btn allow" onClick={allow}>Allow</button>
                    <button className="alert-btn dismiss" onClick={dismiss}>Dismiss</button>
                </div>
            </div>

            <Modal
                show={show}
                onHide={handleClose}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header closeButton>
                    <Modal.Title>Notification</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {message}
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Close
                    </Button>
                    <Button variant="primary">Understood</Button>
                </Modal.Footer>
            </Modal>
        </Container>
    )
}
