import React, { useState } from 'react'
import GoogleMapReact from 'google-map-react';
import styled from 'styled-components';
import AutoComplete from './autocomplete';
import Notification from '../Chart/notification';
import Marker from './marker';

const Wrapper = styled.main`
    width: 100%;
    background: rgba(184, 204, 217, 0.8);
    border-radius: 50px;
    padding: 30px;
`;

const google = window.google = window.google ? window.google : {};
var geocoder, map;


export default function MyGoogleMapReact(props) {

    const [mapApiLoaded, setMapApiLoaded] = useState(false);
    const [mapInstance, setMapInstance] = useState(null);
    const [mapApi, setMapApi] = useState(null);
    const [places, setPlaces] = useState([]);
    const [center, setCenter] = useState([59.3293, 18.0686]); // Must initialize the center
    const [zoom, setZoom] = useState(12);
    const [address, setAddress] = useState('');
    const [draggable, setDraggable] = useState(true);
    const [lat, setLat] = useState(null);
    const [lng, setLng] = useState(null);
    const [bounds, setBounds] = useState(null);
    const [error, setError] = useState();


    // const _onChange = ({ center, zoom }) => {
    //     setCenter([center.lat, center.lng]);
    //     setZoom(zoom);
    //     props.getPlace(places)
    //     props.changeLocation(center);
    // }


    // const addPlace = (place) => {
    //     setPlaces([place]);
    //     setLat(place.geometry.location.lat());
    //     setLng(place.geometry.location.lng());
    //     setCenter([place.geometry.location.lat(), place.geometry.location.lng()]);
    //     props.changeLocation([place.geometry.location.lat(), place.geometry.location.lng()]);
    //     props.getPlace(places);

    //     // _generateAddress()
    // };

    // const _generateAddress = () => {
    //     const mapNewApi = mapApi ? mapApi : {};
    //     const geocoder = Object.keys(mapNewApi).length ? new mapNewApi.Geocoder : null;

    //     geocoder ? geocoder.geocode({ 'location': { lat: lat, lng: lng } }, (results, status) => {
    //         if (status === 'OK') {
    //             if (results[0]) {
    //                 zoom = 12;
    //                 setZoom(12);
    //                 setAddress(results[0].formatted_address);
    //                 props.getAddress(results[0].formatted_address);
    //                 props.getPlace(results[0].formatted_address)
    //             } else {
    //                 window.alert('No results found');
    //             }
    //         } else {
    //             window.alert('Geocoder failed due to: ' + status);
    //         }
    //     }) : '';
    // }

    // Get Current Location Coordinates


    const onMarkerInteraction = (childKey, childProps, mouse) => {
        setDraggable(false);
        setLat(mouse.lat);
        setLng(mouse.lng);
    }

    const onMarkerInteractionMouseUp = (childKey, childProps, mouse) => {
        setDraggable(true);
    }


    const _onClick = (mouse) => {
        console.log(mouse)
        setLat(mouse.lat);
        setLng(mouse.lng);
    }

    const apiHasLoaded = (map, maps) => {
        setMapApiLoaded(true);
        setMapInstance(map);
        setMapApi(maps);

        google.maps.event.addListener(map, "bounds_changed", () => {
            setBounds(map.getBounds());
        });
    };

    const getCenter = (value) => {
        setCenter(value);
        props.changeLocation(value);
    }

    const setCurrentLocation = () => {
        if ('geolocation' in navigator) {
            try {

                const successfulLookup = position => {
                    const { latitude, longitude } = position.coords;
                    fetch(`https://api.opencagedata.com/geocode/v1/json?q=${latitude}+${longitude}&key=6a80181f43a541b58495a4ddf5d90d41`)
                        .then(response => response.json())
                        .then((data) => {
                            if (data && data.results[0] && data.results[0].formatted) {

                                setPlaces(data.results[0].formatted.split(","));
                                props.getPlace(data.results[0].formatted.split(","));
                                props.changeLocation([latitude, longitude]);
                                setCenter([latitude, longitude]);
                                setLat(latitude); setLng(longitude);
                            } else setError(4);
                        }) // Or do whatever you want with the result
                }

                navigator.geolocation.getCurrentPosition(successfulLookup, console.log);

            } catch (error) {
                if (error) {
                    setError(4);;
                }
            }
        }
    }

    React.useEffect(() => {
        if(props.center.length) setCenter(props.center);
    }, [props.center])


    return (
        <Wrapper>
            {mapApiLoaded && (
                <div>
                    <AutoComplete map={mapInstance} mapApi={mapApi} inputChanged={props.inputChanged} getPlace={props.getPlace} changeLocation={props.changeLocation} getCenter={getCenter} />
                </div>
            )}
            <GoogleMapReact
                style={{ width: "100%", height: "305px", margin: "0", padding: "0", position: "relative", borderRadius: "50px" }}
                center={center}
                zoom={zoom}
                draggable={draggable}
                //onChange={_onChange}
                onChildMouseDown={onMarkerInteraction}
                onChildMouseUp={onMarkerInteractionMouseUp}
                onChildMouseMove={onMarkerInteraction}
                onChildClick={() => console.log('child click')}
                onClick={_onClick}
                bootstrapURLKeys={{
                    key: 'AIzaSyCz1wJXBPMIbEqijShDryV0SKF_vf9uO8s',
                    libraries: ['places', 'geometry'],
                }}
                yesIWantToUseGoogleMapApiInternals
                onGoogleApiLoaded={({ map, maps }) => apiHasLoaded(map, maps)}
            >

                <Marker
                    text={address}
                    lat={lat}
                    lng={lng}
                />

            </GoogleMapReact>

            <Notification error={error} />
        </Wrapper >
    )
}   