import * as React from 'react'
import { Navbar, Container, Nav } from 'react-bootstrap'
import { useNavigate } from "react-router-dom";


function Footer() {

    const footerLogoText = React.useRef();
    const aboutElem = React.useRef();
    const clientElem = React.useRef();
    const newsElem = React.useRef();


    const navigate = useNavigate();

    React.useEffect(() => {
        setInterval(() => {
            let logoAnimationFrame = new KeyframeEffect(
                footerLogoText.current,
                [
                    { transform: 'rotate(0deg)' },
                    { transform: 'rotate(360deg)' },
                ],
                {
                    duration: 2000
                }
            )

            let logoAnimation = new Animation(logoAnimationFrame, document.timeline);
            logoAnimation.play();

        }, 4000);
    }, []);

    const routePush = (value) => {

        if (value === 1) navigate('/home');
        else if (value === 2) navigate('/about');
        else if (value === 3) navigate('/client');

    }

    return (
        <div className="footer">
            <Container fluid>
                <Navbar expand="sm" variant="dark">
                    <Nav className="" activeKey="/home">
                        <Nav.Item className="footer-nav" ref={aboutElem} onClick={() => routePush(2)}>
                            About
                        </Nav.Item>
                        <Nav.Item className="footer-nav" ref={newsElem} onClick={() => routePush(1)} >
                            Contact
                        </Nav.Item>

                        <div className="footer-logo-text">
                            .r<div className='footer-logo-animation' ref={footerLogoText} >
                                <div>en</div>
                            </div>
                            bloc
                        </div>

                        <Nav.Item className="footer-nav" ref={clientElem} onClick={() => routePush(3)}>
                            Partners
                        </Nav.Item>
                        <Nav.Item className="footer-nav" ref={newsElem} onClick={() => routePush(1)}>
                            News
                        </Nav.Item>
                    </Nav>
                </Navbar>
            </Container>

            <Container className="social-container">
                <a href="https://www.facebook.com/renbloctechnology" target="_blank">
                    <img src="./img/facebook.png" alt="facebook" />
                </a>
                <a href="https://www.instagram.com/renbloc" target="_blank">
                    <img src="./img/instagram.png" alt="instagram" />
                </a>
                <a href="https://www.linkedin.com/company/renbloc" target="_blank">
                    <img src="./img/linkedin.png" alt="linkedin" />
                </a>
                <a href="https://twitter.com/renbloc" target="_blank">
                    <img src="./img/twitter.png" alt="twitter" />
                </a>
                <a href="https://www.youtube.com/channel/UCpMsV0zxw5diihXZhJJidJQ" target="_blank">
                    <img src="./img/youtube.png" alt="youtube" />

                </a>
            </Container>

            <Container className="footer-info">
                <span>Fleminggatan 18 A - Stockholm, Sweden</span>
                <span>Renbloc - Verified Renewables</span>
                <span>Copyright  Renbloc AB. 2019-2021</span>
            </Container>

        </div>
    )
}

export default Footer;