import React, { useState, useEffect, useRef } from 'react'
import axios from 'axios'
import { Container, Row, Col } from 'react-bootstrap'

import ChartApp from './chartapp'
import Notification from './notification'


export default function Chart(props) {

    const [percent, setPercent] = useState({});
    const [error, setError] = useState(0);
    const chartElement = useRef(null);
    const locationElement = useRef(null);


    const getData = async () => {

        let obj = {
            headers: {
                Authorization: "44f0245f0c2437dd47bf01f236cf2d1ead5f1262",
                x: props.center[1],
                y: props.center[0]
            }
        }

        window.chartElement = chartElement.current;
        window.locationElement = locationElement.current;

        await axios.get("https://renbloc.herokuapp.com/data", obj)
            // await axios.get("http://localhost:3000/data", obj)

            .then(res => {
                if (res.status == 200) {
                    if (res.data.error) {
                        if (res.data.error == 'Server Error!') setError(1);
                        else setError(2);
                    } else {
                        setPercent(res.data);
                    }
                } else {
                    setError(1);
                }
            })

    }

    useEffect(() => {
        if (Object.keys(props.center).length) {
            getData(props.center);
        };

    }, [props.center])


    return (
        <Container fluid className="chart-wrapper" >
            <div className="location container" ref={locationElement} >
                <p > Location </p>
                <h3> {props.place.length ? `${props.place[0]}, ${props.place[1]}` : 'Stockholm, Sweden'} </h3>
                <span> Live update </span>
            </div>

            <div className="chart"
                id="circle-chart"
                ref={chartElement} >
                <Row className="chart-row" >
                    <Col xl="9" >
                        <div className="chart-area" >
                            <ChartApp percent={percent}
                                getPercent={props.getPercent}
                            />
                        </div>
                    </Col>
                    <Col className="legend-column" >
                        <div className="chart-legend container" >
                            <div className="hydro-legend legend" >
                                <div className="hydro-dot dot" >
                                </div>
                                <span>Hydro power</span >
                            </div>
                            <div className="wind-legend legend" >
                                <div className="wind-dot dot" > </div><span>Wind power</span >
                            </div> <div className="solar-legend legend" >
                                <div className="solar-dot dot" > </div><span>Solar power</span >
                            </div> <div className="nonr-legend legend" >
                                <div className="nonr-dot dot" > </div>
                                <span>Non-renewable sources</span >
                            </div>
                        </div>
                    </Col>
                </Row>
            </div>
        </Container >
    )
}