import React, { useState, useEffect } from "react";
import Chart from "react-apexcharts";


export default function ResultChart(props) {
    const [widthOfChart, setWidthOfChart] = useState(340);

    const [options, setOptions] = useState({
        chart: {
            width: 340,
            type: 'donut',
        },
        dataLabels: {
            enabled: true
        },
        responsive: [{
            enabled: true,
            breakpoint: 768,
            options: {
                chart: {
                    width: 250
                },
                legend: {
                    show: false
                }
            }
        }],
        legend: {
            show: false,
        },
        plotOptions: {
            pie: {
                size: 280, // fixed size of the pie/donut, not responsive
                customScale: 0.8,
                expandOnClick: false,
                donut: {
                    size: '75%',
                    labels: {
                        show: true,
                        name: {
                            show: true,
                            fontSize: '50px',
                            color: '#012615',
                            fontFamily: 'Montserrat',
                            fontWeight: 900,
                            formatter: function () {
                                return 0
                            },
                            score: 0
                        },
                        value: {
                            show: true,
                            fontSize: '16px',
                            color: '#012615',
                            fontFamily: 'Scope One',
                            offsetY: -4,
                            fontWeight: 900,
                            formatter: function (val) {
                                return ['RENEWABLE', 'ENERGY']
                            }
                        },
                        total: {
                            show: true,
                            fontSize: '50px',
                            fontFamily: 'Montserrat',
                            offsetY: -4,
                            fontWeight: 900,
                            color: '#012615',
                            formatter: function (w) {
                                return ['RENEWABLE', 'ENERGY']
                            }
                        }
                    }
                }
            }
        }
    })
    const [series, setSeries] = useState([0, 100]);

    useEffect(() => {
        if (props.numberScore && props.score) {
            const score = props.score;
            if(document.querySelector("#chartBoardElement").offsetWidth > 380) setWidthOfChart(360);
            else setWidthOfChart(document.querySelector("#chartBoardElement").offsetWidth);

            if (window.innerWidth <= 768) {
                const newOptions = {
                    chart: {
                        width: widthOfChart,
                        type: 'donut',
                    },
                    colors: ['#9dbf84', '#b8ccd9'],
                    tooltip: {
                        enabled: false
                    },
                    dataLabels: {
                        enabled: false
                    },
                    responsive: [{
                        breakpoint: 740,
                        options: {
                            chart: {
                                width: 250
                            },
                            legend: {
                                show: false
                            }
                        }
                    }],
                    legend: {
                        show: false,
                    },
                    plotOptions: {
                        pie: {
                            size: widthOfChart, // fixed size of the pie/donut, not responsive
                            customScale: 0.8,
                            expandOnClick: false,
                            donut: {
                                labels: {
                                    show: true,
                                    name: {
                                        show: true,
                                        fontSize: '28px',
                                        color: '#012615',
                                        fontFamily: 'Montserrat',
                                        fontWeight: 900,
                                        formatter: function () {
                                            return score
                                        },
                                        score: score
                                    },
                                    value: {
                                        fontSize: '14px',
                                        color: '#012615',
                                        offsetY: -4,
                                        fontFamily: 'Scope One',
                                        fontWeight: 900,
                                        formatter: function (val) {
                                            return ['RENEWABLE', 'ENERGY']
                                        }

                                    },
                                    total: {
                                        show: true,
                                        showAlways: true,
                                        fontSize: '28px',
                                        offsetY: -4,
                                        color: '#012615',
                                        lineHeight: '22.13px',
                                        fontFamily: 'Montserrat',
                                        fontWeight: 900,
                                        formatter: function (w) {
                                            return ['RENEWABLE', 'ENERGY']
                                        }
                                    }
                                }
                            }
                        }
                    }
                }

                setOptions(newOptions)

            }
            else {
                const newOptions = {
                    chart: {
                        width: widthOfChart,
                        type: 'donut',
                    },
                    colors: ['#9dbf84', '#b8ccd9'],
                    tooltip: {
                        enabled: false
                    },
                    dataLabels: {
                        enabled: false
                    },
                    responsive: [{
                        breakpoint: 768,
                        options: {
                            chart: {
                                width: 250
                            },
                            legend: {
                                show: false
                            }
                        }
                    }],
                    legend: {
                        show: false,
                    },
                    plotOptions: {
                        pie: {
                            size: widthOfChart, // fixed size of the pie/donut, not responsive
                            customScale: 0.8,
                            expandOnClick: false,
                            donut: {
                                labels: {
                                    show: true,
                                    name: {
                                        show: true,
                                        fontSize: '50px',
                                        color: '#012615',
                                        offsetY: 10,
                                        fontFamily: 'Montserrat',
                                        fontWeight: 900,
                                        formatter: function () {
                                            return score
                                        },
                                        score: score
                                    },
                                    value: {
                                        fontSize: '16px',
                                        color: '#012615',
                                        fontFamily: 'Scope One',
                                        offsetY: 10,
                                        fontWeight: 900,
                                        formatter: function (val) {
                                            return ['RENEWABLE', 'ENERGY']
                                        }

                                    },
                                    total: {
                                        show: true,
                                        showAlways: true,
                                        fontSize: '50px',
                                        color: '#012615',
                                        offsetY: -4,
                                        fontFamily: 'Montserrat',
                                        fontWeight: 900,
                                        formatter: function (w) {
                                            return ['RENEWABLE', 'ENERGY']
                                        }
                                    }
                                }
                            }
                        }
                    }
                }

                setOptions(newOptions)
            }

            setSeries([Number(props.numberScore), (100 - Number(props.numberScore))]);
        }
    }, [props.numberScore])

    return (
        <div>
            <div className="chart-wrap">
                <div id="chart">
                    <Chart options={options} series={series} type="donut" width={widthOfChart} />
                </div>
            </div>
        </div>
    )
}