var leftWidths = [];


export default function drawAppearAnimation(parentElement, classKey, percentSize, index) {

    /** Draw Appearing Animation */

    let elemWidth;
    let overlap_width = window.innerWidth / 40;
    let left, top;
    let opacity = 0;
    let id = null;

    const element = parentElement.getElementsByClassName(`${classKey}-1`)[0];
    const nextElement = parentElement.getElementsByClassName(`${classKey}-2`)[0];
    element.innerHTML = `${percentSize}<small>%</small>`;

    /**
     * Calculate the width and height of circle and assign width value by percent size
     */
    if (window.innerWidth >= 1920) {
        elemWidth = Math.sqrt(percentSize) * 63;
        elemWidth = elemWidth <= 150 ? elemWidth = 150 : elemWidth;
        if(classKey == "solar") elemWidth = 130;

    }
    else if (window.innerWidth >= 1440 && window.innerWidth < 1920) {
        elemWidth = Math.sqrt(percentSize) * 60;
        elemWidth = elemWidth <= 150 ? elemWidth = 150 : elemWidth;
        if(classKey == "solar") elemWidth = 130;

    }
    else if (window.innerWidth >= 1200 && window.innerWidth < 1440) {
        elemWidth = Math.sqrt(percentSize) * 50;
        elemWidth = elemWidth <= 150 ? elemWidth = 150 : elemWidth;
        if(classKey == "solar") elemWidth = 130;

    }
    else if (window.innerWidth < 1200 && window.innerWidth >= 992) {
        elemWidth = Math.sqrt(percentSize) * 40;
        elemWidth = elemWidth <= 150 ? elemWidth = 150 : elemWidth;
        element.style.fontSize = '40px';
        if(classKey == "solar") elemWidth = 130;
    }
    else if (window.innerWidth < 992 && window.innerWidth >= 768) {
        elemWidth = Math.sqrt(percentSize) * 30;
        elemWidth = elemWidth <= 100 ? elemWidth = 100 : elemWidth;
        element.style.fontSize = '24px';
        if(classKey == "solar") elemWidth = 90;


    }
    else if (window.innerWidth >= 576 && window.innerWidth < 768) {
        elemWidth = Math.sqrt(percentSize) * 25;
        elemWidth = elemWidth <= 100 ? elemWidth = 100 : elemWidth;
        element.style.fontSize = '28px';
        if(classKey == "solar") elemWidth = 90;
    }
    else if (window.innerWidth < 576) {
        if (index === 0) elemWidth = window.innerWidth / 2.5;
        else if (index === 1) elemWidth = window.innerWidth / 3.5;
        else if (index === 2) elemWidth = window.innerWidth / 4.5;
        else if (index === 3) elemWidth = window.innerWidth / 5.5;
        
        elemWidth = elemWidth <= 70 ? elemWidth = 70 : elemWidth;
        if(classKey == "solar") elemWidth = 50;
        element.style.fontSize = '20px';
    }

    element.style.width = (elemWidth) + "px";
    element.style.height = (elemWidth) + "px";
    element.style.lineHeight = (elemWidth) + "px";

    /**
     *  Calculate the position of starting point
     */

    left = 0; top = 600;
    var windowWidth = window.innerWidth;

    switch (index) {
        case 0:
            if (windowWidth <= 576 && windowWidth >= 320) {
                leftWidths.push((windowWidth * 0.9 - elemWidth));
            } else if (windowWidth > 576 && windowWidth <= 768) {
                leftWidths.push((windowWidth * 0.8 - elemWidth));
            } else if (windowWidth > 768 && windowWidth <= 992) {
                leftWidths.push((windowWidth * 0.7 - elemWidth));
            } else if (windowWidth > 992 && windowWidth <= 1200) {
                leftWidths.push((windowWidth * 0.7 - elemWidth));
            } else if (windowWidth > 1200) {
                leftWidths.push((windowWidth - elemWidth) / 2);
            } else {
                leftWidths.push((windowWidth * 0.9 - elemWidth));
            }
            break;
        case 1:
            leftWidths.push(leftWidths[0] - elemWidth );
            leftWidths.push(elemWidth);
            break;
        default:
            break;
    }

    /**
     * Draw animation.
     */



    id = setInterval(appearFrame, 1);

    function appearFrame() {
        if (index == 0 && top <= 0) {
            clearInterval(id);
        }
        else if (index == 0 && top > 0) {
            top -= 600 / 300;
            var _left;
            if (windowWidth <= 576 && windowWidth >= 320) {
                _left = windowWidth * 0.9 - elemWidth;
            } else if (windowWidth > 576 && windowWidth <= 768) {
                _left = windowWidth * 0.8 - elemWidth;
            } else if (windowWidth > 768 && windowWidth <= 992) {
                _left = windowWidth * 0.7 - elemWidth;
            } else if (windowWidth > 992 && windowWidth <= 1200) {
                _left = windowWidth * 0.7 - elemWidth;
            } else if (windowWidth > 1200) {
                _left = (windowWidth - elemWidth) / 2;
            } else {
                _left = windowWidth * 0.9 - elemWidth;
            }
            left += _left / 300;
            opacity += 1 / 300;
            element.style.opacity = opacity;
            element.style.top = top + "px";
            element.style.left = left + "px";
        }
        else if (index == 1 && top <= 0) {
            clearInterval(id);
        }
        else if (index == 1 && top > 0) {
            top -= 600 / 300;
            left += (leftWidths[0] - elemWidth + overlap_width) / 300;
            opacity += 1 / 300;
            element.style.opacity = opacity;
            element.style.top = top + "px";
            element.style.left = left + "px";
        }
        else if (index == 2 && top <= 0) {
            clearInterval(id);
        }
        else if (index == 2 && top > 0) {
            left += (leftWidths[1] - elemWidth + overlap_width + (20 / 1920 * window.innerWidth)) / 300;
            top -= 600 / 300;
            opacity += 1 / 300;
            element.style.opacity = opacity;
            element.style.left = left + "px";
            element.style.top = top + "px";

        }
        else if (index == 3 && top <= (leftWidths[2] - overlap_width)) {
            leftWidths = [];
            clearInterval(id);
        }
        else if (index == 3 && top > (leftWidths[2] - overlap_width)) {
            top -= (600 - leftWidths[2] + overlap_width) / 300;
            left += (leftWidths[0] - elemWidth + overlap_width) / 300;
            opacity += 1 / 300;
            element.style.opacity = opacity;
            element.style.top = top + "px";
            element.style.left = left + "px";
        }
    }

    nextElement.className = nextElement.className.replace(/.$/, "1");
    element.className = element.className.replace(/.$/, "2");

}